
 /////////
 //PROGRESS
 ////////
.progress {
	display: none;
	& > * {
		position: relative;
		z-index: 10;
	}
	margin-bottom: 130px;

}

 .steps-progress {

 	width: 	100%;
 	.flex();
 		max-width: 945px;
 			margin: 0 auto;
 	.steps-progress__title-mob {
 		color: rgba(0,0,0,.11);
 		display: none;
 		margin-bottom: 	16px;
 	}

 	.mobile-text {
		text-align: center;
		width: 	100%;
		display: none;	
	}
 }

 .steps-progress__part {
 	flex: 1 1 33%;
 	color: @blackOp;
 	span {
 		display: block;
 		width: 100%;
 		height: 4px;
 		background: @blackOp;
 		position: relative;
 		.color {
 			position: absolute;
 			z-index: 2;
 			top: 0;
 			left: 0;
 			width: 0;
 			height: 100%;
 			background: @progress1;
 			transition: all 0.4s linear;
 		}
 	}
 }

 .steps-progress__part {
 	&:nth-child(3) {
 		span {
 			.color {
 				background: @progress2;
 				width: 0;
 			}
 		}
 	}
 }

  .steps-progress__part {
 	&:last-child {
 		span {
 			.color {
 				background: @progress3;
 				width: 0;
 			}
 		}
 	}
 }

 .steps-progress__title{
 	font-size: 1rem;
 	margin-bottom: 18px;
 	transition: all 0.3s ease;

 }

 .step-progress__count {
 	padding-left: 	20%;
 	font-size: 21px;
 	font-family: 'Roboto', sans-serif;
 	transition: all 0.3s ease;
 }




//////////
/////FORM
//////////


.step1__form {
	width: 100%;
	max-width: 968px;
	margin: 0 auto;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	// margin-bottom: 75px;
}
.step1__form-wrap {
	flex: 0 1 100%;
	display: flex;
	justify-content: space-between;

}
.step1__form-group {
	flex: 0 1 50%;
	max-width: 430px;
	min-width: 280px;
	margin: 0 10px;
	&:nth-child(-n+3) {
		margin-bottom: 35px;
	}

	span {
		font-size: 10px;
		color: red;
		display: none;
	}
}



.form-input {
	border: 1px solid rgba(21, 15, 61, 0.34);
	border-radius: 12px;
	color: rgba(0, 0, 0, 0.68);
	text-align: left;
	padding: 10px 50px 10px 26px ;
	font-size: 1rem;
	line-height: 148.69%;
	outline: none;
	width: 100%;
	background: @white;
	&::-webkit-input-placeholder {
		color: rgba(0, 0, 0, 0.30);
	}

	&::-moz-placeholder {
		color: rgba(0, 0, 0, 0.30);
	}

	&:focus {
		background-color: #fff;
    border-color: @blue;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(@blue,.25);
	}
}

select  option:disabled {
	color: rgba(0, 0, 0, 0.30);
}

.step1-btn, .step2-btn, .step3-btn {
	margin: 0 auto;
	display: block;
	&:disabled {
		background: #ccc;
		&:hover {
			box-shadow: unset;
		}
	}
}


.form-input.city {
	 padding-right: 25px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.select-label {
	position: relative;
	display: block;
	&:after {
	content: "▼";
  padding: 0 8px;
  font-size: 12px;
  position: absolute;
  right: 40px;
  top: 25%;
  vertical-align: middle;
  z-index: 1;
  text-align: center;
  width: 10%;
  height: 100%;
  pointer-events: none;
  box-sizing: border-box;	
	}
}


.form-label {
	position: relative;
	svg {
		position: absolute;
		width: 20px;
		height: 20px;
		fill: #656289;
		right: 20px;
		top: 50%;
		transform: translateY(-50%);
	}


}


.form-group__title {
	font-weight: 700;
	margin-bottom: 21px;
}

/////
////Acquaintance///
/////

.step1__acquaintance {
	width: 100%;
	.flex(@align-items: stretch);
	box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.06);
	margin-bottom: 72px;
}

.step1-count {
	font-size: 36px;
	font-family: Roboto, sans-serif;
	line-height: 148.69%;
	margin-right: 	35px;
	margin-left: 5px;
}

.step1__acquaintance_right {
	flex: 0 1 51.4%;
	img {
		height: 100%;
	}
}

.step1__acquaintance_left {
	flex: 0 1 48.6%;
	position: relative;
}

.step1-wrap {
	.flex(@justify-content: flex-start);
	position: absolute;
	max-width: 207/535 * 100%; 
	top: -50%;
	left: 0;

	img {
		max-width: 144/207 * 100%;
	}
}

.step1-text {
	padding-top: 13%;
	padding-right: 45px;
	padding-left: 59px;
	padding-bottom: 37px;
	font-weight: 500;
	font-size: (18/16) * 1rem;
	line-height: 148.69%;
}