.step2__gift {

	font-weight: 300;
	line-height: 178.69%;
	max-width: 975px;
	margin: 0 auto;
	margin-bottom: 	89px;
	span {
		font-weight: 900;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			width: 100%;
			background: linear-gradient(184.46deg, #49DEFF 1.27%, #E95FFF 103.77%);
