.step2__free-gift {
	box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.06);
	max-width: 1005px;
	margin: 0 auto;
}

.step2__free-gift_top {
	.flex();
	padding-top: (60/1005) * 100%;
	padding-left: (49/1005) * 100%;
	padding-right: (126/1005) * 100%;
	align-items: flex-start;
}

.step2__free-gift-text {
		flex: 0 1 652/829 * 100%;
		text-indent: 1rem;
		font-size: 18px;
		line-height: 148.69%;
		font-weight: 500;
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		margin-bottom: 	37px;
		padding-top: 12px;
		p {
			margin-bottom: 	24px;
		}
		a {
			color: @black;
			display: 	inline-block;
			font-size: 1rem;
			text-indent: 0;
			text-decoration: underline;
			transition: all 0.2s linear;
			display: flex;	
			justify-content: center;
			align-items: center;
			svg {
				width: 	24px;
				height: 	24px;
				margin-left: 	15px;
				transition: 0.15s linear;
			}
			&:hover, &:focus {
				color: @blue;
				svg {
					fill: @blue;
					opacity: 1;
				}
			}
		}
}

.step2__free-gift-img {
flex: 0 1 100% - (652/829 * 100%);
}

