.step2__free-sale {
	padding-top: 39px;
	padding-right: 27px;
	padding-bottom: 30px;
	.flex();
	width: 	auto;
	justify-content: flex-end;
	p {
		font-family: Roboto, sans-serif;
		font-size: 1rem;
		line-height: 148.69%;
		color: rgba(0, 0, 0, 0.25);
		margin-right: 	42px;
		& > span {
			position: relative;	
			font-weight: 700;
			font-size: (22/16) * 1rem;
			margin-left: 	16px;
			&:before {
				content: '';
				position: absolute;
				width: 	100%;
				height: 	2px;
				background: @black;
				top: 50%;
				left: 	0;
			}
			& > span {
					font-size: (18/16) * 1rem;
					margin-left: 	6px;
					font-weight: 400;
			}
		}
	}
	& > span{
		font-family: Roboto;
		font-style: normal;
		font-weight: 900;
		font-size: (22/16) * 1rem;
		line-height: 148.69%;
	& > span {
		font-size: (18/16) * 1rem;
		margin-left: 	6px;
		}
	}
}


