////
//Switcher
////

	 span.switcher {
    position: relative;
    margin-right: 	41.5px;
    
    width:52px;
    height:26px;
    border-radius:25px;
    display: block;	
    input {
       appearance: none;
       margin: 	0;
       position: relative;

       width:52px;
       height:26px;
       border-radius:25px;

       background-color:@blue;
       outline:none;
    }
    label {
       z-index:1;
       position: absolute;
       top:6px;
       left: 	0;
       height: 15px;
       border-radius:15px;
    }
    &.switcher-1 {
       input {
          transition:.25s -.1s;
          &:checked {
             background:@blue;
             transition: background .5s -.1s;
             &+label {
                left:32px;
                right:6px;
                
                background:@white;
                
                transition: left .4s .2s, right .5s, background .35s -.1s;
             }
          }
          &:not(:checked)  {
             background-color:@grey;
             &+label {
                left:6px;
                right:31px;
								
                background:@blue;
                
                transition: left .5s, right .4s .2s;
             }
          }
       }
    }
 }


