.header {
	position: relative;
	z-index: 1;

}

.header__layer {
	& > * {
		position: relative;
		z-index: 2;
	}
	background: linear-gradient(120.44deg, #5F15FC 15.24%, #57CFFA 87.7%);
	height: 100%;
	position: relative;
	.fPa(@pt:18%, @pb:18%, @ptpx:18px, @pbpx:18px, 1140px, 1440px);
	margin-bottom: 	68px - 29px;

	.f-container {
		flex-wrap: nowrap;
	}

}

.header-user, .header-links {
	.flex();
}

.header-links {
	flex: 0 1 193px;
}

.header-user {
	flex: 0 1 650px;
	margin-right: 25px;
}

.header-user__greeting {
	font-weight: 900;
	color: @white;
	.fSize(23vw, 23px * (992/1140), 23px, 992px, 1140px);
	br {
		display: none;	
	}

}

.header-link {
	font-size: (17/16) * 1rem;
	// transition: all 0.25s linear;
	&:hover {
		border-bottom: 1px dashed @white;
	}
}

.header-user__ava {
	svg {
		fill: @light-grey;
		max-width: 41.86px;
		max-height: 41.86px;
	}
}

.header-user__ava {
	width: 80px;
	height: 80px;
	padding-top: 5px;
	border-radius: 50%;
	position: relative;
	background: @white;
	display: flex;
	justify-content: center;
	align-items: center;
	&:hover {
		&:after {
			background: @main-color - 45;
		}
	}
	&:after {
		content: '+';
		bottom: -3px;
		right: -4px;
		color: #fff;
		display: flex;
		justify-content:center;
		align-items: center;
		background: @main-color;
		font-size: 1.8rem;
		line-height: 1.8rem;
		font-weight: 400;
		width: 24px;
		height: 24px;
		border-radius: 50%;
		position: absolute;
		border: 1px solid @white;
		z-index: 3;
		cursor: pointer;
		transition: all 0.2s linear;
		
	}
	&:before {
		content: '';
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 50%;
		border: 18px solid @white;
		position: absolute;
		z-index: 2;
	}
	& > input {
		width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
	}
}