	.thanks__layer {
		& > * {
			position: relative;
			z-index: 3;
		}
		.fPa(@pt:0, @pb:125%, @ptpx:0, @pbpx:125px, 576px, 1200px);
		.fMa(@mt:180%, @mb:100%, @mtpx:180px, @mbpx:100px, 576px, 1200px);
		position: relative;
		&:before {
			content: '';
			position: absolute;
			width: 622px;
			height: 622px;
		background: linear-gradient(#6850FF 0%, #50EAFF 100%);
		border-radius: 50%;
		right: -622px/1.8;
		bottom:-622px/4.5;
		}
	}

.thanks__content {

	width: 100%;
	max-width: 945px;
	background: @white;
	.flex(@align-items: flex-start);
	box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.06);
	padding: (64/1140)*100% (48/1140)*100% (72/1140)*100% (27/1140)*100%;
	margin: 0 auto;
	position: relative;
	margin-bottom: 126px;
	&:before {
		content: '';
		background: linear-gradient(180deg, #6850FF 0%, #50EAFF 60.94%);
		width: 496.63px;
	height: 496.63px;
	position: absolute;
	border-radius: 50%;
	transform: rotate(-42.9deg);
	z-index: -1;
	    top: -35%;
    left: -18%;

	}

	.thanks__arrow{
		position: absolute;
		bottom: -25px;
		right: 100%/6;
		max-height: 70px;
		max-width: 20px;
	}

	img {
		position: relative;
		top: -91px;
		flex: 0 1 153px/945px * 100%;
		margin-right: 15px;
		max-width: 16.19047619%;
		min-width: 61px;
		max-height: 176/466 * 100%;
		height: auto;
	}
}

.thanks__textes {
	flex: 0 1 100% - (153px/945px * 100%);
}

.thanks__name, .thanks__goodbye {

	font-weight: 900;
	font-size: (22/16) * 1rem;
	line-height: 148.69%;


}

.thanks__name {
	margin-bottom: 18px;
}

.thanks__subtitle, .thanks__paragraph {

	font-weight: 500;
	font-size: (18/16) * 1rem;
	line-height: 148.69%;

	}
	.thanks__paragraph {
		margin-bottom: 20px;
	}

.thanks__subtitle {
	text-indent: 1rem;
	margin-bottom: 25px;
}


.thanks__list-item {

	font-weight: 700;
	font-size: (18/16) * 1rem;
	line-height: 148.69%;
	position: relative;
	margin-left: 291px - 196px;
	svg {
		position: absolute;
		top: 0;
		left: -35px;
		max-height: 17px;
		max-width: 23px;
		fill: #7E42FF;
	}

	&:not(:last-child) {
		margin-bottom: 15px;
	}

	&:last-child {
		margin-bottom: 32px;
	}

}

.thanks__textes-wrap {
	img {
		display: none;
	}
}




	.free-download {
		width: 100%;
	}

	.free-download__text {
		text-align: center;
		font-weight: 900;
		.fSize(34vw, 34px * (768/1200), 34px, 768px, 1200px);
		margin-bottom: 43px;

	}
	.free-download__links {
	
		margin: 0 auto;
		.flex(@justify-content: center);
	}

	.free-download__link {
		transition: all 0.25s linear;
			&:first-child {
			margin-right: 17px;
		}

		&:hover {
			transform: translateY(-5px) scale(1.08);

		}
	}