@import url('app/less/_var.less');
@import url('app/less/_fonts.less');
@import url('app/less/_mixins.less');




html {
	font-size: 16px;
}

html, body {
	overflow-x: hidden; 

	

}

::selection {
		background: @light-blue;
		color: @white;
	}
body 
{
	margin: 0;
	padding: 0;	
	font-family: 'Raleway', sans-serif;	
	font-size: 17px;
	font-weight: 500;
	color: @black;
	letter-spacing: 1.15px;
	letter-spacing: 0.03rem;
	line-height: 148.69%;



}

div, a {
	outline: none;
}



*, *:before, *:after {
	box-sizing: border-box;
}

img
{
	max-width: 100%;
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
	padding: 0;
	margin: 0;
}

h2 {

	overflow-wrap: break-word;
}


ul , li {
	list-style: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
	padding: 0;
	margin: 0;
	display: block;
	color: @white;
	cursor: pointer;

}


input {
	width: 100%;
	outline: none;
	border: none;
	text-align: center;
}



.center {
	max-width: 1140px + 30px + 30px;
	width: 100%;
	margin: 0 auto;
	padding: 0 30px;

}



.f-container {

	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	flex-direction: row;
}


// .icon-svg {
// 	.sprite(@s-svg);
// }

.current-step {
	padding-bottom: 4%;
}




//|||||||||||||||||||COMMON ELEMENTS|||||||||||||||||||//


.main-btn {

	background: @blue;
	width: 100%;
	max-width: 226px;
	letter-spacing: 0.05rem;
	font-family: 'Roboto', sans-serif;	
	font-weight: 900;
	font-size: 1rem;
	padding: 13px 25px;
	text-align: center;
	// border-radius: 4px;
	color: @white;
	outline: none;
	border: none;
	width: 	100%;
	transition: all 0.25s ease;
	cursor: pointer;

	
	&:focus {
		
	}

	&:hover {
		background: @blue + 35;
		// box-shadow: 0px 0px 25px @blue;
	}
}



.h2-title {
	.fSize(48vw, 48px/2, 48px, 576px, 1280px);
	font-weight: 700;
	font-family: 'MuseoSansCyrl', sans-serif;
	text-align:center;
	position: relative;	

}

