.step3__layer {
	& > * {
		position: relative;
		z-index: 2;
	}
}


.step3-wrap {
	position: relative;
	box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.06);
	width: 100%;
	flex: 0 1 (555/1140) *100%;
	margin-right: 30px;
	background: @white;
}


.step3-image__wrap {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
}
.step3-wrapper {
	.flex(@align-items: flex-end);
}
.step3-text {
	text-align: center;
	padding-top: 9px;
	&.payment {
		.fSize(36vw, 36px * (992/1200), 36px, 992px, 1200px);
		font-weight: 900;
		line-height: 148.69%;
		// margin-left: 	15%;
		padding: 0;
	}

		padding: 9px 45px 37px 59px;
		text-align: left;
	
}

.step3-count {
	font-size: 36px;
	font-family: Roboto, sans-serif;
	line-height: 148.69%;
	margin-right: 15px;
	margin-top: 45px;
	margin-left: 5px;
}

.step3-payment {
	.flex(@flex-wrap: wrap, @align-items: flex-start);
	// margin-bottom: 88px;
}

.step3-calc {
	flex: 0 1 ((261+218)/1140) *100%;
	margin-right: 	auto;
	background: url(../img/payment.png) no-repeat right 10%;
	background-size: 43%;
	margin-top: 50px;
}

.step3-calc__title {
font-weight: 500;
font-size: 18px;
line-height: 150.19%;
margin-bottom: 4px;

}

.step-calc__sum {
	margin-bottom: 10px;
	&:first-of-type {
		span {
		font-size: 1.375rem;
		font-weight: 700;
		font-family: Roboto, sans-serif;
		}
		
	}
}

.step3-btn {
	margin-left: 0;
	display: flex;
	justify-content: space-between;
	align-items: center;
	text-align: center;
	svg {
		fill: @white;
		max-width: 45px;
		max-height: 25px;

	}
}

.step-calc__sum.free {
	.flex();
	width: 	auto;
	justify-content: flex-start;
	p {
		font-family: Roboto, sans-serif;
		font-size: 1rem;
		line-height: 148.69%;
		color: rgba(0, 0, 0, 0.25);
		margin-right: 	25px;
		& > span {
			position: relative;	
			font-size: (22/16) * 1rem;
			font-weight: 700;
			// margin-left: 	16px;
			&:before {
				content: '';
				position: absolute;
				width: 	100%;
				height: 	2px;
				background: @black;
				top: 50%;
				left: 	0;
			}
			& > span {
					font-size: (18/16) * 1rem;
					margin-left: 	6px;
					font-weight: 400;
			}
		}
	}
	& > span{
		color: @blue;
		font-style: normal;
		font-weight: 900;
		font-size: (18/16) * 1rem;
		line-height: 148.69%;
	& > span {
		font-size: (18/16) * 1rem;
		margin-left: 	6px;
		}
	}

	&:nth-child(6	) {
		margin-bottom: 22px;
	}
}


.step3-total {
	width: 100%;
		
   margin-right: 9%;
   	margin-bottom: 42px;
	.flex(@justify-content: flex-start);
	span {
		.fSize(45vw, 45px * (952/1200), 45px, 952px, 1200px);
	font-family: Roboto, sans-serif;
	font-weight: 900;
	line-height: 148.69%;

	&:nth-child(2) {
		color: rgba(0,0,0,.25);
		position: relative;
		margin-right: 30px;
		&:before {
				content: '';
				position: absolute;
				width: 	110%;
				height: 	2px;
				background: @black;
				top: 50%;
				left: 	0;
			}
	}
	}
}

.step3-total__text {
	margin-right: 13.6%;

}



//////
//MESSAGE
/////

.step3-message {
	position: relative;
	background: @white;
	width: 100%;
	max-width: 553px;
	margin: 0 auto;
	margin-bottom: 90px;
	margin-top: -40px;
	box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.06);
	padding: (35/1140)*100% (53/1140)*100% (46/1140)*100% (60/1140)*100%;
	img {
		position: absolute;
		top: 12px;
		left: -33px;
	}
	&:before {
		content: "";
		position: absolute;
		width: 525px;
		height: 525px;
		background: linear-gradient(180deg, rgba(150, 85, 255, 0.23) 0%, rgba(253, 159, 255, 0.15) 100%);
		border-radius: 50%;
		top: -110%;
		left: -50%;
		z-index: -1;
	}
}


.step3-important__text {
	margin-bottom: 30px;
	font-size: (26/16) * 1rem;
	margin-left: 16px;
}
.step3-important__title{
	font-weight: 900;
	font-size: (24.5/16) *1rem;
	margin-bottom: 30px;
	margin-left: 16px;
}

.step3-important__paragraph {
	position: relative;
	font-size: (21/16) * 1rem;
	&:not(:last-child) {
			margin-bottom: 30px;
		}
	span {
		font-weight: 700;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			bottom: -6px;
			left: 0;
			background:  #7E42FF;
			height: 2px;
			width: 100%;
		}
	}
	&:before {
		content: '';
		position: absolute;
		width: 10px;
		height: 10px;
    top: 6px;
    left: -25px;
		background: #7E42FF;
		font-size: 21px;
		
	}
}

.step3-total__payment-btn {
	.flex(@align-items: flex-start);
	img {
		border-radius: 50%;
	}
}

.step3-text.payment {
	flex: 0 1 555px - 207px;
	padding-left: 42px;
	padding-bottom: 28px;
}

.step3-image__wrap {
	flex: 0 1 207px;
	justify-content: space-between;
}

.step3-hidden__img {
	display: none;
}

.step3-btn {
	margin: 0 auto;
	display: block;
}