.greeting__layer {
	.f-container {
		flex-direction: column;
	}
}


.step1__greeting {
	box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.06);
	.flex(@align-items: flex-start);
	position: relative;
	width: 100%;

	
	max-width: 751px;
	margin: 	0 auto;
	margin-bottom: 71px;
}

 .step1__greeting-img {
	width: 20%;
 	min-width: 103px;
 	position: absolute;
 	padding: 3px 27px/2 35px 10px;
 	opacity: 1;
 	// transform: translateY(-80px);
 	transition: transform 1.5s linear, opacity 2.5s linear;
 }

 // .showed .step1__greeting-img {
 // 	opacity: 1;
 // 	transform: translateY(0);

 // }

 .step1__greeting-text {
 	flex: 0 1 100%;
 	font-size: (17/16) * 1rem;
 	line-height: 148.69%;
 	padding: 29px 36px 35px 27px/2;
 	letter-spacing: 0.03rem;
 
 }

 .step1__greeting_bold {
 	font-weight: 900;
 	// font-family: Roboto, sans-serif;
 }

 .step1__greeting_semibold {
 	font-weight: 700;
 }

 .step1__greeting-paragraph {
 	&:first-child {
 		margin-bottom: 	12px;
 		padding-left: 20%;

		text-indent: 1rem;
 	}
 	&:last-child {
 		padding-left: 20%;
 	}

 	br {
 		display: none;
 	}
 }