.step2__layer {
	// .fPa(@pt:0%, @pb:40%, @ptpx:0px, @pbpx:40px, 1200px, 1440px);
}

.step2__top {
	width: 100%;
	
	position: relative;
	z-index: 2;
	&>* {
		position: relative;
		z-index: 2;
	}
}

.step2.current-step {
	padding-bottom:	0;
}

.step2-layer_middle {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	margin-bottom: 55px;
}

.step2__congratulation {
	flex: 0 1 48.6%;
	flex-wrap: wrap;
	display: flex;
	justify-content: space-between;
}

.step2-bloggers {
	flex: 0 1 51.4%;
	justify-content: space-between;
	padding-left: 62.5px;
	padding-right: 87px;
	font-size: (14/16) * 1rem;
	font-family: Roboto, sans-serif;
	line-height: 18/14;
	max-height: 357px;
	overflow-y: auto;
	&::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
	border-radius: 10px;
}

&::-webkit-scrollbar
{
	width: 10px;
	background-color: #F5F5F5;
}

&::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	background-image: linear-gradient(185.02deg,#49deff 1.27%,#e95fff 103.77%);
}

	.subscribers {
		color: rgba(0, 0, 0, 0.34);
	}

	.location {
		color: rgba(0, 0, 0, 0.34);
	}
}
.step2__blogger-name {
	display: flex;
	justify-content: flex-end;
	margin-bottom: 18px;
}
.step2__blogger-card {
	.flex(@align-items: stretch);
	&:not(:last-child) {
		margin-bottom: 27.59px;
	}
	img {
		border-radius: 5px;
		margin-right: 20px;
		transition: all 0.3s linear;
	}

	&:hover {
		img {
		transform: translate(4px) scale(1.08);
    box-shadow: 0px 10px 10px 0rem rgba(@black, 0.25)
		}
	}
}

.step2__blogger-wrap{
	flex: 0 1 80%;
}

.step2__blogger-subscribers {
	.flex();
}

.step2__blogger-location:extend(.step2__blogger-subscribers){};

.step2-layer_top {
	display: flex;
	justify-content: flex-end;
}

.step2__paragraph {
	flex: 0 1 51.4%;
	font-size: 26px;
	font-weight: 300;
	line-height: 148.69%;
	margin-bottom: 62px + 4px;
	span:first-child {
		font-weight: 700;
	}

	span:last-child {
		position: relative;
		&:before {
			width: 100%;
			height: 2px;
			content:"";
			position: absolute;
			bottom: -10px;
			left: 0;
			background: linear-gradient(185.02deg, #49DEFF 1.27%, #E95FFF 103.77%);
			box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.06);
		}
	}
}


.step2-count {
	font-size: 36px;
	font-family: Roboto, sans-serif;
	line-height: 148.69%;
	margin-right: 	35px;
	margin-left: 5px;
}

.step2-text {
	line-height: 197.19%;
	span {
		font-size: 22px;
		font-weight: 700;

	}

	.line {
		display: none;
	}
}

.step2-wrap {
	position: relative;
	box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.06);
	width: 100%;
	margin-bottom: 49px;
}


.step2-image__wrap {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    position: absolute;
    top: -50%;
    left: 0;
}

.step2-text {
	text-align: center;
	padding-top: 19px;
	&:last-child {
		padding: 15px 45px 37px 59px;
		text-align: left;
	}
}

.step2-offers {
	width: 100%;
}

.step2-offer__numbers {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin-bottom: 18px;
	span {
		width: 32px;
		height: 32px;
		border: 1px solid #BD69FF;
		border-radius: 6px;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 29px;
	}
	svg {
		width: 21px;
		height: 10px;
		fill:  #656289;
	}

	.number {
		font-weight: 700;
		font-size: 24px;
		color: @white;
		background: @main-color;
		padding: 7px 16px;
		padding-top: 3px;
		border-radius: 26.5px;
	}
}

.step2-offer__description {
	font-size: 1rem;
	font-weight: 700;
	line-height: 148.69%;
	width: 100%;
	max-width: 359px;
	padding-left: 11%;
}

.step2-offer {
	&:first-child {
		margin-bottom: 47px;
	}
}

.step2__blogger-btn {
	color: @black;
	font-weight: 700;
	background: rgba(189, 105, 255, 0.12);
	transition: all 0.25s linear;
	padding: 20px 45px;
	border-radius: 6px;
	text-align: center;
	width: 100%;
	max-width: 360px;

	&:hover {
		background: rgba(189, 105, 255, 0.12) + 35;
		// box-shadow: 0px 0px 25px rgba(189, 105, 255, 0.12) + 50;
		color: @white;
	}
}

.step2-layer_bottom {
	position: relative;
}

.step2-bottom__wrap {
	max-width: 215px+114px+360px;
	.flex(@align-items: flex-end);
	margin-left: auto;
	margin-right: 261px - 149px;
	margin-bottom: 68px;
}

.step2-hidden {
	position: relative;
	display: none;
	top: 30px;
	width: 100%;
	height: 100%;
	padding-left: 279px - 243px + 92px;
	padding-right: 92px;
	z-index: 10;
	// margin-bottom: 	-120px;

	.close-btn {
		position: absolute;
		cursor: pointer;
		    top: -30px;
    right: 55px;
		width: 45px;
		height: 45px;
		&:hover {
			span {
				&:first-child {
					transform: translate(-50%, -50%) rotate(0);
					background: @main-color;	
				}

				&:last-child {
					transform: translate(-50%, -50%) rotate(180deg);
					background: @main-color;
				}
				
			}
		}
		span {
			position: absolute;
			width: 25px;
			height: 2px;
			top: 50%;
			left: 50%;
			background: rgba(0, 0, 0, 0.22);
			display: block;
			margin: auto;
			transition: all 0.15s linear;
			&:first-child {
				transform: translate(-50%, -50%) rotate(45deg);
				
			}

			&:last-child {
				transform: translate(-50%, -50%) rotate(135deg);
				
			}
		}	
	}

}

.step2-hidden__card {
	box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.06);
	background: @white;
	position: relative;

	&:not(:last-child) {
		border-bottom-left-radius: 46px;
		border-bottom-right-radius: 46px;
		z-index: 2;
	}

	&:nth-child(2) {
		z-index: 5;
		top: 0px;
	}
	
	&:nth-child(3) {
		z-index: 4;
		top: -40px;
	}

	&:nth-child(4) {
		z-index: 3;
		top: -80px;
	}
	&:last-child {
		z-index: 2;
		top: -120px;
	}

	&:not(:nth-child(2)) {
		padding-top: 73px + 40px;
	}
}

.step2-hidden__card {
	.flex();
	padding-right: 68px;
	padding-left: 56px;
	padding-top: 73px;
	&:not(:nth-child(2)) {
		padding-bottom: 76px;
		
	}
	&:not(:nth-child(even)) {
		padding-left: 94px;
		padding-right: 102.74px;
	}
	&:nth-child(2){	
		position: relative;
		overflow: hidden;
		&:before {
			content: '';
			position: absolute;
			top: -144%;
    	left: -38%;
			width: 537/920 * 100%;
			height: 537/214.41 * 100%;
			background: rgba(189, 105, 255, 0.1);
			border-radius: 46px;
			transform: matrix(0.8, -0.6, 0.6, 0.8, 0, 0);
		}
	}
	padding-bottom: 28px;
	svg {
	fill: rgba(189, 105, 255, 0.5);
	max-height: 34.62px;
	max-width: 58px;
		}
	img {
		max-width: 89px;
	}
}

.step2-hidden__text {
	flex: 0 1 616/(917-68-56) * 100%;
	font-size: 1rem;
	position: relative;
	line-height: 176.69%;
	&:before {
		content: '';
		position: absolute;
		top: -5px;
		left: -41px;
		width: 17.87px;
		height: 17.87px;
		border-radius: 3px;
		background: #BD69FF;
	}
}

.step2-btn {
	margin: 	0 auto;
}

.step2__bg-image {
		margin-right: 25px;
		min-width: 175px;
}


.step2__show-more {
	margin: 0 auto;
	display: none;
}

.step2-layer_middle .oveflow {
	overflow-y: visible;
	max-height: 100%;
	animation: heig 500ms linear forwards;
}

.opacity {
	animation: heig 500ms linear forwards;
}

@keyframes heig {
	100% {
			opacity: 1;
	}
}

.step2__paragraph.mobile-paragraph {
	display: none;
}

.start-ads {
	margin: 0 auto;
	display: block;
}

.step2-about__bloggers {
	overflow-y: hidden;
	padding-bottom: 	4%;
	padding-top: 30px;
	// position: absolute;	
}

@import url('part2-next.less');